/* NavBar Styles */
.navbar{
    background-color: #fcd750;
    position: fixed;
    /* top: 0; */
    width: 100%;
    padding: 1.6rem 0;
}
.flex{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 1.6rem;
}
.flex a{
    height: 25px;
}
.logo{
    object-fit: cover;
    max-width: 100%;
    width: 130px;
}
.nav-icon{
    display: inline-block;
    width: 30px;
    height: 25px;
    color: #2d2c4c;
}  